import React, {useState} from 'react'
import './Header.css'
import logo from '../public/logo_big.svg'
import '../Animation.css'
import jQuery from "jquery";
let $ = window.jQuery = jQuery;

function Header(props) {
    $(function(){
        $(window).scroll(function(){ 
            var num = $(this).scrollTop();
            if( num > 284 ){ 
                $(".menu-wrapper").css("position","fixed");
                $(".link").css("visibility","visible");
                $(".menu-wrapper").css("top","20px");
                var width = ($(window).width()-$(".menu-wrapper").outerWidth())/2;
                $(".menu-wrapper").css("left",width);
                }
                else {
                    $(".menu-wrapper").css("position","");
                    $(".link").css("visibility","hidden");

               }
        });
    });    
    return (
        <div>
        <div className="animated fadeInDown header-wrapper">
            <div className="header">
                <img src={logo}/>
            </div>
        </div>

            <div className="animated fadeInDown menu-wrapper">
                <div className="menu">
                    <div className="menu-selector">
                        <a herf="#" onClick={()=>props.k(1)}>ABOUT</a>
                    </div>
                    <div className="menu-selector">
                        <a herf="#" onClick={()=>props.k(2)}>POST</a>
                    </div>
                    <div className="menu-selector">
                        <a herf="#" onClick={()=>props.k(3)}>GALLERY</a>
                    </div>
                    <div className="menu-selector">
                        <a herf="#">GITHUB</a>
                    </div>
                    
                </div>
            </div>
        </div>
    )
}
export default Header;